import React from 'react';
import './AgentInfo.css';
import {CardTitle, Card, CardActions, CardText, DataTable, TableHeader} from 'react-mdl';

import imageyoru from '../../Images/Yoru_artwork.webp';
function Yoru() {
    return(
                <div className="main">
                    <div className='main-2'>Yoru</div>
                    <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                        <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imageyoru} style={{maxWidth:'35%', paddingLeft:'30%'}} alt="imageyoru"/></CardTitle>
                        <CardText>
                            Japanese native Yoru rips holes straight through reality to infiltrate enemy lines unseen. Using deception and aggression in equal measure, he gets the drop on each target before they know where to look.
                            </CardText>
                        <CardActions>
                            <DataTable className='table'
                                shadow={0}
                                rows={[
                                    {ability: 'Fakeout', cost: '100 Creds'},
                                    {ability: 'Blindside', cost: '200 Creds'},
                                    {ability: 'Gatecrash', cost: 'Free per round'},
                                    {ability: 'Dimensional Drift', cost: '7 Points'}
                                ]}
                            >
                                <TableHeader name="ability">Special Abilities</TableHeader>
                                <TableHeader numeric name="cost">Cost</TableHeader>
                            </DataTable>
                        </CardActions>
                    </Card>
                </div>
    );
}

export default Yoru;